import React, { Component } from 'react'

import avatar from '../static/avatar.png'

export default class Avatar extends Component {
  render() {
    const width = this.props.imgWidth
    const height = this.props.imgHeight
    return (
      <div>
        <img 
          width={width}
          height={height}
          style={{
            borderRadius: '50%'
          }}
          src = {avatar}
          alt='avatar'
        />
      </div>
    )
  }
}
