import React from 'react';
import ReactDOM from 'react-dom';
import './index.css'

import 'antd/dist/antd.css'; // or 'antd/dist/antd.less'

import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom"; 

import { Provider } from 'react-redux'
import store from './redux/store'

import SignIn from './pages/SignIn'
import SignUp from './pages/SignUp/SignUp'
import SetPassword from './pages/SignUp/SetPassword'
import SetName from './pages/SignUp/SetName'
import ForgotPassword from './pages/ForgotPassword'
import Explore from './pages/Explore/Explore'
import Villiage from './pages/Villiage'
import WriteStory from './pages/WriteStory'
import Me from './pages/Me'
import App from './pages/App'
import Hot from './pages/Hot'
import Welcome from './pages/Welcome';
import VerifyAccount from './pages/SignUp/VerifyAccount';
import ExploreContent from './pages/Explore/ExploreContent';
import BestList from './pages/Explore/BestList';

class Index extends React.Component {
  render() {
    return (
    <Router>
        <Switch>
          <Route exact path='/'>
            <Welcome />
          </Route>
          <Route path="/app">
            <App>
              <Route path="/app/explore">
                <Explore>
                  <Route path = "/app/explore/explorecontent" component={ ExploreContent } />
                  <Route path = "/app/explore/bestlist" component = { BestList } />
                </Explore>
              </Route>
              <Route path="/app/villiage">
                <Villiage />
              </Route>
              <Route path="/app/writestory">
                <WriteStory />
              </Route>
              <Route path="/app/hot" component={Hot} />
              <Route path="/app/me">
                <Me />
              </Route>
            </App>
          </Route>
          <Route path="/login">
            <SignIn />
          </Route>
          <Route path='/signup'>
            <SignUp>
              <Route exact path='/signup/' component={SetPassword} />
              <Route path='/signup/setname' component={SetName} />
              <Route path='/signup/verify' component={VerifyAccount} />
            </SignUp>
          </Route>
          <Route path='/forgotpassword' >
            <ForgotPassword />
          </Route>
        </Switch>
    </Router>
    )
  }
}


ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
      <Index />
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);