import React, { Component } from 'react'

import heartIcon from '../../../static/heart-icon.png'

import { Avatar } from 'antd';
import { UserOutlined } from '@ant-design/icons';

//mock
import img_2 from '../../../static/demo-2.jpg'

export default class BestContent extends Component {
  render() {
    return (
      <div
        style = {{
          backgroundColor: '#eaf',
          height: '12rem',
          margin: '1rem',
          marginBottom: '0',
          borderRadius: '1rem',
          display: 'flex',
          flexDirection: 'column',
          boxShadow: '3px 3px 7px -4px'
        }}
      >
        <img
          src = {img_2}
          alt = ''
          style = {{
            alignSelf: 'flex-start',
            width: '100%',
            height: '75%',
            borderRadius: '1rem 1rem 0 0'
          }}
        />
        <div
          style = {{
            backgroundColor: '#fff',
            alignSelf: 'flex-end',
            width: '100%',
            height: '25%',
            padding: '0 0.5rem',
            borderRadius: '0 0 1rem 1rem'
          }}
        >
          <p
            style = {{
              marginBottom: '0'
            }}
          >
            今天是异地恋的第282天，也是我们在一...
          </p>
          <div
            style = {{
              display: 'flex',
              justifyContent: 'space-between'
            }}
          >
            <span>
              <Avatar size = {20} icon={<UserOutlined />} />
              <author> 史迪仔</author>
            </span>
            <span>
              <img
                src = {heartIcon}
                alt = '赞'
                width = '15'
              />
              &nbsp;1.5w
            </span>
          </div>
        </div>
      </div>
    )
  }
}
