const accountReducer = (state = { login: false }, action) => {
  switch(action.type) {
    case 'ACCOUNT_SIGN_IN':
      return {
        ...state,
        login: true
      }
    case 'ACCOUNT_SIGN_OUT':
      return {
        ...state,
        login: false
      }
    default:
      return state
  }
}

export default accountReducer
