import React, { Component } from 'react'
import Navigator from './Navigator'

import { Link } from 'react-router-dom'

import exploreIcon from '../static/explore-icon.png'
import villiageIcon from '../static/villiage-icon.png'
import hotIcon from '../static/hot-icon.png'
import meIcon from '../static/me-icon.png'
import writeStoryIcon from '../static/writestory-icon.png'

export default class NavigationBar extends Component {
  render() {
    return (
      <div
        style = {{
          position: 'fixed',
          width: '100%',
          height: '4rem',
          paddingTop: '0.5rem',
          backgroundColor: '#fff',
          borderTop: '1px solid grey',
          bottom: '0',
          display: 'flex',
          justifyContent: 'space-evenly'
        }}
      >
        <Navigator 
          content = '探索'
          icon = {exploreIcon}
          size = '25'
          to = '/app/explore/explorecontent'
        />
        <Navigator
          content = '故事村'
          icon = {villiageIcon}
          size = '25'
          to = '/app/villiage'
       />
        <Navigator
          icon = {writeStoryIcon}
          size = '35'
          to = '/app/writestory'
       />
        <Navigator
          content = '热点'
          icon = {hotIcon}
          size = '25'
          to = '/app/hot'
       />
        <Navigator
          content = '我的'
          icon = {meIcon}
          size = '25'
          to = '/app/me'
       />
      </div>
    )
  }
}
