import React, { Component } from 'react'

import mockList from './mock'

import AutoResponsive from'../src'

import heartIcon from '../../../static/heart-icon.png'
import { Avatar } from 'antd';
import { UserOutlined } from '@ant-design/icons';
import FormalContent from './FormalContent';
import BestContent from './BestContent';

import '../../../static/demo-2.jpg'

let getItemStyle = (i) => {
  const deviceWidth = document.body.clientWidth
  let width = Math.floor(deviceWidth * 0.43)
  let height = 240
  if(deviceWidth >= 600) {
    width = 170
    height = 260
  }
  if(deviceWidth >= 1000) {
    width = 235
    height = 270
  }
  return {
    //👴列表中每个元素容器的样式
    width,
    height,
    backgroundColor: '#edc',
    borderRadius: 10,
    boxShadow: '2px 2px 20px -8px'
  };
};


class WaterfallSampleComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      styleList: {},
      List: []
    };
  }

  componentWillMount() {
    this.renderStyle(this.state.List)
  }

  renderStyle = (List) => {
    const styleList = {}
    List.map((i, index) => {
      styleList[index] = getItemStyle(i);
    });
    this.setState({
      styleList
    })
  }

  onListScroll = (e) => {
    const scrollTop = document.documentElement.scrollTop
    const clientHeight = document.documentElement.clientHeight
    const scrollHeight = document.documentElement.scrollHeight
    const isBottom = (scrollHeight - clientHeight - scrollTop )
    if(isBottom <= 1) {//👴在这里处理加载新的内容
      this.handleAdd()
    }
  }

  componentDidMount() {
    this.handleAdd()
    window.addEventListener('resize', () => {
      this.setState({
        containerWidth: document.body.clientWidth
      });
    }, false);
    window.addEventListener('scroll', this.onListScroll, false);
  }

  getAutoResponsiveProps() {
    return {
      itemMargin: 15,
      containerWidth: this.state.containerWidth,
      itemClassName: 'item',
      transitionDuration: '.8',
      transitionTimingFunction: 'easeIn'
    };
  }

  handleAdd = () => {
    const addList = mockList
    const List = this.state.List
    const newList = List.concat(addList)
    this.renderStyle(newList)
    this.setState({
      List: newList
    })
  }

  render() {
    return (
      <div>
        <AutoResponsive ref="container" {...this.getAutoResponsiveProps()}>
          {
            this.state.List.map((i, index) => {
              return (
                <div key={index} className="item" style={this.state.styleList[index]}>
                  <FormalContent
                    img = {i.img}
                    tittle = {i.tittle}
                    author = {i.author}
                    star = {i.star}
                  />
                </div>
              )
            })
          }
        </AutoResponsive>
      </div>
    );
  }
}


export default class InfiniteList extends Component {
  render() {
    return (
      <div
        style = {{
          border: '1px siolid',
          paddingLeft: '1rem',
          marginTop: '1rem'
        }}
      >
        <WaterfallSampleComponent />
      </div>
    )
  }
}
