import React, { Component } from 'react'

import coinIcon from '../../../static/coin-icon.png'

import { Link } from 'react-router-dom'

class Button extends Component {
  render() {
    return (
      <Link to = {this.props.to} >
        <div
          style ={{
            backgroundColor: '#A7C4F0',
            width: '4.5rem',
            height: '2.5rem',
            borderRadius: '1.5rem',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            color: '#fff'
          }}
        >
          {this.props.content}
        </div>
      </Link>
    )
  }
}

export default class ExploreNavigator extends Component {
  render() {
    return (
      <div
        style = {{
          width: '100%',
          height: '2.9rem',
          paddingRight: '1rem',
          paddingLeft: '1rem',
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center'
        }}
      >
      <Button content = '探 索' to = '/app/explore/explorecontent' />
      <Button content = '排 行 榜' to = '/app/explore/bestlist' />
      <div
          style ={{
            width: '4.5rem',
            height: '2.5rem',
            borderRadius: '1.5rem',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            fontSize: '1rem'
          }}
        >
          <img
            src = { coinIcon }
            alt = '故事点'
            width = '25rem'
          />
          { this.props.balance }
        </div>
      </div>
    )
  }
}
