import React, { Component } from 'react'

import { Input, Tooltip, Modal } from 'antd'
import NextButton from './components/NextButton'

export default class SetName extends Component {

  constructor(porps) {
    super(porps)
    this.state = {
      setName: ''
    }
  }

  handleChange = (e) => {
    this.setState({
      setName: e.target.value
    })
  }

  handleNext = () => {
    if(this.state.setName === '') {
      //🕵️‍♂️你还没取名字呢
      Modal.warning({
        title: '错误',
        content: (
          <p>请先给你的商铺取个名字</p>
        )
      })
      return
    } else {
      this.props.history.push('/signup/verify')
    }
  }

  render() {
    const tipTitle = this.state.setName === '' ?
    '输入商铺名'//前置信息
    :
    '好名字'//输入名字后的提示信息，如名字重复、不合法等
    return (
      <div>
        <div
          style = {{
            height: '39rem',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'space-evenly'
          }}
        >
          <h1>给你的商铺取个名字</h1>
          <div>
            <Tooltip
              trigger={['focus']}
              title={ tipTitle }
              placement="top"
            >
              <Input
                onChange = { this.handleChange }
              />
            </Tooltip>
          </div>
          <NextButton onClick = { this.handleNext } />
        </div>
      </div>
    )
  }
}
