import React, { Component } from 'react'

import NextButton from './components/NextButton'

import { 
  Input,
  Modal,
  Tooltip
} from 'antd'

export default class SetPassword extends Component {
  constructor(props) {
    super(props)
    this.state = ({
      setPassword: '',
      verifyPassword: ''
    })
  }

  handleSetPassWord = (e) => {
    this.setState({
      setPassword: e.target.value
    })
  }

  handleVerifyPassword = (e) => {
    this.setState({
      verifyPassword: e.target.value
    })
  }

  handleNext = () => {
    const setPassword = this.state.setPassword
    const verifyPassword = this.state.verifyPassword
    if(setPassword === '') {
      Modal.warning({
        title: '错误',
        content: (
          <p>请先设置你的密码</p>
        )
      })
      return
    } else {
      if(setPassword !== verifyPassword) {
        Modal.warning({
          title: '错误',
          content: (
            <p>两次密码输入不一致，请检查</p>
          )
        })
        return
      } else {
        let obj = {
          pathname: '/signup/setname',
          search: '?sort=name',
          query: { fromDashboard: 123 },
          state: { fromDashboard: 123 }
        }
        this.props.history.push(obj)
      }
    }
  }

  render() {
    const setPasswordTitle = this.state.setPassword === '' ?
    '请输入你的密码'//前置信息
    :
    '长度：x-x, 请不要用emoji作为你的密码'//填入密码后的提示，（长度、字符
    return (
      <div>
        <div
          style = {{
            height: '39rem',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'space-evenly'
          }}
        >
          <h1>设置你的密码</h1>
          <div
            style = {{
              width: '80%',
              height: '6rem',
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'space-between'
            }}
          >
            <Tooltip
              trigger={['focus']}
              title={setPasswordTitle}
              placement="top"
            >
              <Input.Password
                onChange = { this.handleSetPassWord }
                addonBefore="你的密码"
                size = 'middle'
              />
            </Tooltip>

            <Input.Password
              onChange = { this.handleVerifyPassword }
              addonBefore = "确认一下"
              disabled = { this.state.setPassword === '' }
            />
          </div>
          <NextButton
            onClick = { this.handleNext }
          />
        </div>
      </div>
    )
  }
}
