import React, { Component } from 'react'

import { connect } from 'react-redux'
import accountSignIn from '../redux/Actions/account_sign_in'

import { withRouter, Link } from 'react-router-dom'

class Welcome extends Component {
  componentDidMount() {
    console.log('welcome')
    this.judgeSigninStatus()
  }
  
  //👻看看当前登陆状态
  judgeSigninStatus = () => {
    const signInfo = JSON.parse(localStorage.getItem('signInfo'))
    
    if(signInfo === null) {//👶第一次访问页面，没登陆过
      return
    } else {//🙎‍♂️曾经登陆过，localStorage里面有相关信息
      if(signInfo.signin) {//🙂已登录
        this.props.dispatch(accountSignIn)
        this.props.history.push('/login')
        return
      } else {//🙁未登录
        this.props.history.push('/login')
        return
      }
    }
  }

  render() {
    return (
      <div>
        <p>welcome to 故事贩卖铺</p>
        <Link to='/login'>去登陆</Link>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return state
}

//通过connetct() 连接组件和redux数据
export default connect(mapStateToProps)(withRouter(Welcome));
