import React, { Component } from 'react'

import searchIcon from '../../../static/search-icon.png'
import { Link } from 'react-router-dom'

import { 
  Row,
  Col
 } from 'antd'

export default class ExploreHead extends Component {
  render() {
    return (
      <div>
        <Row style = {{
          height: '6rem'
        }} >
          <Col flex={3}>
            <div
              style = {{
                width: '100%',
                height: '100%',
                paddingLeft: '0.5rem',
                color: '#000',
                fontWeight: '600',
                fontSize: '28px',
                lineHeight: '2.5rem'
              }}
            >
              <h>
                Choose the story you<br/> want to read.
              </h> 
            </div>
          </Col>
          <Col flex={2}>
            <div
              style = {{
                width: '100%',
                height: '100%',
                paddingRight: '1rem',
                display: 'flex',
                flexDirection: 'row-reverse',
                alignItems: 'center'
              }}
            >
              <Link>
                <img 
                  src = {searchIcon}
                  alt = '搜索'
                  width = '30'
                  height = '30'
                />
              </Link>
            </div>
          </Col>
        </Row>
      </div>
    )
  }
}
