import React, { Component } from 'react'

import { connect } from 'react-redux'
import accountSignOut from '../redux/Actions/account_sign_out'
import accountSignIn from '../redux/Actions/account_sign_in'

import { withRouter } from 'react-router-dom'

//临时写的一个状态检查工具
class Checkout extends Component {

  componentDidMount() {
    this.judgeSigninStatus()
  }
  
  //👻看看当前登陆状态
  judgeSigninStatus = () => {
    const signInfo = JSON.parse(localStorage.getItem('signInfo'))
    
    if(signInfo === null) {//👶第一次访问页面，没登陆过
      this.props.history.push('/login')
      return
    } else {//🙎‍♂️曾经登陆过，localStorage里面有相关信息
      if(signInfo.signin) {//🙂已登录
        this.props.dispatch(accountSignIn)
        return
      } else {//🙁未登录
        this.props.history.push('/')
        return
      }
    }
  }

  //注销登录
  handleSignOut = () => {
    //改redux的state
    this.props.dispatch(accountSignOut)
    //改localSotrage
    const signInfo = JSON.parse(localStorage.getItem('signInfo'))
    signInfo.signin = false
    localStorage.setItem('signInfo', JSON.stringify(signInfo))
    this.judgeSigninStatus()
  }

  storeState = () => {
    const login = this.props.login
    if(login) {
      return ('已登录')
    } else {
      return ('未登录')
    }
  }

  localStorageState = () => {
    const login = JSON.parse(localStorage.getItem('signInfo')).signin
    if(login) {
      return ('已登录')
    } else {
      return ('未登录')
    }
  }

  render() {
    return (
      <div
      style = {{
        border: '1px solid pink',
        display: 'flex',
        justifyContent: 'space-evenly',
        fontSize: '12px'
      }}
      >
        <p>redux store中的状态： {this.storeState()}</p>
        <p>localStorage 中的状态：{this.localStorageState()}</p>
        <button onClick={this.handleSignOut}>注销登录</button>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return state
}

//通过connetct() 连接组件和redux数据
export default connect(mapStateToProps)(withRouter(Checkout));
