import React, { Component } from 'react'

export default class VerifyAccount extends Component {
  
  goToApp = () => {
    this.props.history.push('/app')
  }

  render() {
    return (
      <div>
        <div
          style = {{
            height: '39rem',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'space-evenly'
          }}
        >
          <h1>注册成功！</h1>
          <div
            style = {{
              lineHeight: '2rem'
            }}
          >
            <p>你的ID号是：</p>
            <p>你的商铺叫：</p>
            <p>你的密码为：</p>
          </div>
          <button
            onClick = {this.goToApp}
          >好了，我们开始吧</button>
        </div>
      </div>
    )
  }
}
