import React, { Component } from 'react'

import backIcon from '../static/返回icon.png'

import { Link } from 'react-router-dom'

export default class TitleBar extends Component {
  render() {
    const left = this.props.left
    return (
      <div
        style = {{
          borderBottom: '1px solid grey',
          height: '2.5rem',
          width: '100%',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between'
        }}
      >
        <Link to = { this.props.backTo }>
          <div
            style = {{
              display: 'flex'
            }}
          >
            <img
              src = { backIcon }
              alt = '返回'
              width = '20'
              style = {{
                marginLeft: '0.3rem'
              }}
            />
            {this.props.backInfo}
          </div>
        </Link>
        <span
          style = {{
            position: 'absolute',
            left,
            textAlign: 'center'
          }}
        >{ this.props.tittle }</span>
        <div
          style = {{
            marginLeft: '0.3rem'
          }}
        >{this.props.logic}</div>
      </div>
    )
  }
}
