import React, { Component } from 'react'
import { connect } from 'react-redux'
import accountSignIn from '../redux/Actions/account_sign_in'

import { withRouter } from 'react-router-dom'

import NavigationBar from '../components/NavigationBar'

class App extends Component {

  componentDidMount() {
    this.judgeSigninStatus()
  }
  
  //👻看看当前登陆状态
  judgeSigninStatus = () => {
    const signInfo = JSON.parse(localStorage.getItem('signInfo'))
    
    if(signInfo === null) {//👶第一次访问页面，没登陆过
      this.props.history.push('/login')
      return
    } else {//🙎‍♂️曾经登陆过，localStorage里面有相关信息
      if(signInfo.signin) {//🙂已登录
        this.props.dispatch(accountSignIn)
        return
      } else {//🙁未登录
        this.props.history.push('/')
        return
      }
    }
  }



  render() {
    return (
      <div>
        {this.props.children}
        <NavigationBar />
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return state
}

//通过connetct() 连接组件和redux数据
export default connect(mapStateToProps)(withRouter(App));
