import React, { Component } from 'react'
import { connect } from 'react-redux'
import accountSignIn from '../redux/Actions/account_sign_in'

import { withRouter, Link } from 'react-router-dom'

import Input from '../components/Inputer'
import Avatar from '../components/Avatar'

class SignIn extends Component {
  
  //登录按钮
  handleSignIn = () => {
    this.props.dispatch(accountSignIn)
    console.log('登陆成功')

    //登录成功后将登陆信息存放到localStorage中
    const signInfo = {
      signin: true,
    }
    localStorage.setItem('signInfo',JSON.stringify(signInfo))
    this.props.history.push('/app')
  }

  render() {
    return (
      <div
        style = {{
          height: '100vh',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center'
        }}
      >
        <span
         style = {{
          marginTop: '6rem'
         }}
        >
          <Avatar imgWidth={'100'} imgHeight={'100'} />
        </span>
        <div>
          <Input
            style={{
              margin: '2rem'
            }}
            info={'ID号：'} 
          />
          <Input 
            style={{
              margin: '2rem'
            }}
            info={'密码：'} 
          />
        </div>
        <button onClick={this.handleSignIn} >开始探索</button>
        <div
          style={{
            position: 'absolute',
            bottom: '1rem',
            width: '100%',
            display: 'flex',
            justifyContent: 'space-evenly'
          }}
        >
          <Link to='/signup' >注册</Link>
          <Link to='/forgotpassword' >找回密码</Link>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return state
}

//通过connetct() 连接组件和redux数据
export default connect(mapStateToProps)(withRouter(SignIn));
