import React, { Component } from 'react'

import heartIcon from '../../../static/heart-icon.png'
import { Avatar } from 'antd';
import { UserOutlined } from '@ant-design/icons';

import demojpg from '../../../static/demo-3.jpg'

export default class FormalContent extends Component {
  render() {
    return (
      <div
        style = {{
          width: 'inherit',
          height: '100%',
          borderRadius: 'inherit',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'space-between'
        }}
      >
        <div
          style = {{
            width: '100%',
            height: '80%',
            borderRadius: '10px 10px 0 0'
          }}
        >
          <img
            src = {this.props.img}
            alt = '.'
            style = {{
              width: '100%',
              height: '100%',
              borderRadius: '10px 10px 0 0'
            }}
          />
        </div>
        <div
          style = {{
            width: 'inherit',
            height: '20%',
            borderRadius: '0 0 10px 10px',
            padding: '0 0.3rem',
            backgroundColor: '#fff'
          }}
        >
          <p
            style = {{
              marginBottom: '0'
            }}
          >
            {this.props.tittle}
          </p>
          <div
            style = {{
              display: 'flex',
              justifyContent: 'space-between'
            }}
          >
            <span>
              <Avatar size = {20} icon={<UserOutlined />} />
              <author> {this.props.author}</author>
            </span>
            <span>
              <img
                src = {heartIcon}
                alt = '赞'
                width = '15'
              />
              &nbsp;{this.props.star}
            </span>
          </div>
        </div>
      </div>
    )
  }
}
